

.budget-section {
    position: relative;
    width: 100%;
    min-height: 100vh;
    /* background-color: #67AB3F; */
    background: linear-gradient(to right, #ffffff, #000000);
    overflow: visible;
    display: flex;
    flex-direction: column;
}

.budget-logo-title {
    display: flex;
    align-items: center;
    padding: 20px;
    z-index: 1;
    margin-top: 30px;
    margin-left: 30px;
}

.budget-logo {
    width: 50px;
    /* Adjust as needed */
    margin-right: 10px;
    border-radius: 0%;
}

.budget-title {
    font-size: 16px;
    /* Adjust as needed */
    color: #000000;
}

.budget-content {
    margin-top: 4rem;
    margin-left: 3rem;
}

.budget-content h1 {
    font-size: 50px;
    font-family: 'Spongition', sans-serif;
    position: relative;
    color: #000000;
    margin-bottom: 1rem;
}

.budget-content h1::after {
    content: "";
    position: absolute;
    bottom: 3px; /* Adjust the distance below the text */
    left: 0;
    width: 20%; /* The underline will take up % of the width */
    height: 6px; /* Thickness of the underline */
    background-color: #222222;
}

.budget-content h2 {
    color: #000000;
    font-size: 20px;
}

.budget-section a {
    margin: 0 auto;
    margin-top: 2rem;
    margin-left: 3rem;
    border: 1.5px solid #000000;
    border-radius: 30px;
    padding: 3rem 4rem;
    transition: all 500ms ease;
    text-decoration: none; 
}


