.budget-file svg {
    color: #000000;
    width: 80px;
    height: 80px;
    transition: all 500ms ease;
}

.budget-file p {
    margin-top: 1rem;
    color: #000000;
    text-align: center;
    transition: all 700ms ease; 
}

.budget-section a:hover {
    border: 1.5px solid #222222;
}

.budget-file:hover svg {
    color: #222222;
}

.budget-file:hover p {
    color: #222222;
}


